import VueHeader from 'components/common/header.vue'
import service from 'service'
  export default{
    components:{
      VueHeader
    },
    data(){
      return {
        list:[]
      }
    },
    methods:{
      getList(){
        service.category_list.get().then(data=>{
          console.log(data);
          this.list = data.body.data.list;
        })
      }
    },
    created(){
      this.getList();
    }
  }



