export default{
  name:'vue-auto-img',
  props:{
    list:{
      type:Object,
      default:{}
    }
  },
  data(){
    return {}
  },
  methods:{
    parseStyle(data){
      let style = {};
      style.width = data.w / 100 + 'rem';
      style.height =data.h / 100 + 'rem';
      style.top = data.y/100 + 'rem';
      style.left = data.x/100 + 'rem';
      return style
    }
  }
}



